.gallery__elements {
    list-style: none;
    padding: 0;
    margin: 0;
    display: grid;
    grid-template-columns: repeat(auto-fit, 282px);
    gap: 20px 17px;
}

@media screen and (min-width: 320px) and (max-width: 767px) {
    .gallery__elements {
        margin: 0 19px;
    }
}

